.btn-ghost {
  @include button-variant($ghost, $ghost, $ghost, transparent, $ghost, transparent);
  color: $link-color;
  border-color: transparent;
}

%btn-custom-base {
  font-family: 'penumbra-serif-std', serif;
  padding: .5em 1.2em 0.2em 1.2em;
  font-weight: bold;
  color: #d1dde4;
  border-color: #a17f3c;
  border-width: 2px;
  box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.3);
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.2);

  &:hover {
    color: #d1dde4;
  }
}

.btn-custom-primary {
  @extend %btn-custom-base;

  background: linear-gradient(to bottom, #3583b4, #225b8b);

  &:hover {
    background: linear-gradient(to bottom, #378abd, #296799);
  }
}

.btn-custom-secondary {
  @extend %btn-custom-base;

  background: linear-gradient(to bottom, #262d35, #121519);

  &:hover {
    background: linear-gradient(to bottom, #30363e, #1b1e22);
  }
}


