body {
  background-image: url(../images/background.png);
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover;
}

hr {
  background: rgba(129, 129, 129, 0.25);
}

header {
  background-image: url(../images/footer.png);
  background-position: center;
  padding: .7em 0;
  border-bottom: 1px solid #423a26;
}

#app {
  min-height: 100vh;
}

.main {
  padding: 2em 0;
}

.nav-custom {
  border-top: 2px solid #15243a;
  border-right: 2px solid #15243a;
  border-bottom: 2px solid #15243a;
  border-left: 2px solid #15243a;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.5), inset 0 0 0 2px #4e5765;
  flex: 1 0 0;
  position: relative;
  height: 3em;
  margin: 1.6em 0 0 0;
  padding: 0 0 0 .5em;
  border-width: 1px;
  border-radius: 5px;
  background: #203658;
  font-size: .9em;
}

.navbar-brand img {
  width: 50%;
}

.active:hover,
.active {
  color: #ecbc62;
  background: radial-gradient(ellipse at center, rgba(237, 181, 72, 0.1) 0%, rgba(237, 181, 72, 0.1) 20%, rgba(237, 181, 72, 0) 70%, rgba(237, 181, 72, 0) 100%);
}

.content {
  border-top: 6px solid #0f1428;
  border-bottom: 6px solid #0f1428;
  padding: 1.5em 0;
}


.post {
  padding: 0.5em 1em;
  text-align: left;
  margin: 0 0 1em 0;
}

.post img {
  border: 0px !important;
  max-width: 100%;
  height: auto;
  margin: 2em 0;
  transition: box-shadow .2s;
  max-width: 40em;
}

.post img:hover {
  box-shadow: 0 0 0 5px rgba(255, 255, 255, 0.1);
}

.post-wrapper img {
  display: block;
  box-sizing: border-box;
  width: 100%;
  border: 1px solid #423a26;
  margin: 0 0 1em 0;
}

.home-post {
  padding: 20px;
  text-align: left;
  transition: background .2s;
}

.home-post img {
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.home-post img:hover {
  box-shadow: 0 0 0 5px rgba(255, 255, 255, 0.1);
}

.home-post:hover {
  background: $ghost;
}

.post-header {
  padding: 0 0 1em 0;
}

.post-header h1,
.home-post h1 {
  position: relative;
  margin: 0;
  font-size: 26px;
  font-weight: 600;
  text-transform: uppercase;
  padding-bottom: 5px;
  letter-spacing: 1px;
  padding-top: .2em;
  font-family: 'penumbra-serif-std';
  color: #c1d7e6;
}

.home-post-wrapper img {
  display: block;
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 0.5em;
  border: 1px solid #423a26;
}

.sidebar {
  text-align: left;
  border-radius: 5px;
  background: rgba(210, 233, 247, 0.04);
  padding: 25px;
}

.sidebar h3 {
  margin: 0;
}

footer {
  background-image: url(../images/footer.png);
  border-top: 1px solid #423a26;

  .logo {
    background: url(../images/footer_logo.png) no-repeat 0 0;
    display: block;
    width: 230px;
    height: 118px;
    background-size: contain;
  }

  .footer-disclaimer {
    color: rgba(183, 206, 221, 0.3);
  }

  .footer-links {
    padding: 1em 0;
  }

  .footer-links a {
    font-size: 1em;
    color: #b7cedd;
    text-transform: uppercase;
  }

  .footer-links span {
    display: inline-block;
    height: 1.2em;
    width: 2px;
    margin: 0 .8em;
    background: #443e30;
    vertical-align: middle;
  }
}

.button-black {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  padding: 2px;
  white-space: nowrap;
  border-radius: 5px;
  border: 0;
  background: #816632;
  cursor: pointer;
}

.button-inner {
  display: block;
  position: relative;
  z-index: 101;
  padding: 0 1.6em 0 1.6em;
  font-family: 'penumbra-serif-std', serif;
  font-weight: bold;
  color: #ccd9e0;
  line-height: 2.8;
  height: 2.6em;
  text-transform: uppercase;
  box-sizing: border-box;
  background: linear-gradient(to bottom, #262d35, #121519);
  box-shadow: inset 0 0 0 2px #060b11, inset 0 0 0 3px rgba(127, 100, 49, 0.3);
}

.button-blue {
  color: #ffffff;
  display: inline-block;
  position: relative;
  z-index: 0;
  height: 2.5em;
  padding: 0 1.2em 0 1.2em;
  border-radius: 5px;
  border: 1px solid #a17f3c;
  background: linear-gradient(to bottom, #3583b4, #225b8b);
  box-sizing: border-box;
  box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.3);
  line-height: 2.5em;
  font-family: 'penumbra-serif-std', serif;
  font-weight: bold;
}

.button-blue:hover {
  color: #ffffff;
  background: linear-gradient(to bottom, #378abd, #296799);
}

.input-wrapper {
  text-align: left;
}


.input-wrapper label {
  display: block;
  margin-bottom: .5em;
  color: #b7cedd;
}

.input-wrapper input {
  width: 270px;
  padding: .8em 1em;
  margin-bottom: 1em;
  border: 1px solid #302f2c;
  border-radius: 4px;
  background: #0c1523;
  box-sizing: border-box;
  font-family: "Noto Sans", sans-serif;
  font-size: .9em;
  color: #b7cedd;
  transition: border 0.2s, background 0.2s, box-shadow 0.2s, color 0.2s;
}

.input-wrapper input:focus {
  outline: none;
  color: #ffffff;
  border-color: #ab8742;
}

.post-hr {
  margin: 2em 0;
}

.post-credit {
  font-size: 1.1em;
  margin-top: 4px;
  margin-bottom: 10px;
  color: #b8cfde;
  line-height: 1.5em;
}

.post-credit span {
  color: #7f8ba7;
}

.post-author {
  text-align: center;
}

.post-author img {
  width: 10em;
  border-radius: 0;
  border: 5px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 1.2em;
}

.post-author-gm img {
  border: 5px solid #ffcc00;
}

.post-author-gm h4 {
  color: #ffcc00;
  background: radial-gradient(ellipse at center, rgba(237, 181, 72, 0.1) 0%, rgba(237, 181, 72, 0.1) 20%, rgba(237, 181, 72, 0) 70%, rgba(237, 181, 72, 0) 100%);
}

.post-author h4 {
  color: #c1d7e6;
  margin: 0;
  padding: 0;
}

.post-author p {
  padding: 0;
  margin: 0;
}

.post-footer p {
  display: inline-block;
  margin-right: .5em;
}

.post-list {
  background: rgba(255, 255, 255, 0.04);
  padding: .7em;
  margin-bottom: .7em;
  cursor: pointer;
  transition: background .2s;
}

.post-list:hover {
  background: rgba(255, 255, 255, 0.1);
}

.post-list p {
  padding: 0;
  margin: 0;
}

.post-list h4 {
  color: #b7cedd;
  font-family: "Noto Sans", sans-serif;
  margin: 0;
  padding: 0 0 .2em 0;
  flex: 0;
  font-size: 1em;
  text-transform: unset;
}

.post-list img {
  width: 100%;
}



