
.navbar-hytale {
  .navbar-brand span {
    background: url(../images/logo.png) no-repeat 0 0;
    display: block;
    width: 65px;
    height: 93px;
    background-size: contain;
  }

  .nav-item {
    .nav-link {
      font-family: 'penumbra-serif-std', serif;
      color: #a5b9c6;
      text-transform: uppercase;
      font-weight: 700;
      font-size: 1.1em;
      line-height: 2em;

      &:hover {
        color: $link-color;
      }
    }

    &.active {
      .nav-link  {
        color: $link-color;
        background: radial-gradient(ellipse at center, rgba(237, 181, 72, 0.1) 0%, rgba(237, 181, 72, 0.1) 20%, rgba(237, 181, 72, 0) 70%, rgba(237, 181, 72, 0) 100%);
      }
    }
  }
}




